.SelfEmployed {
    margin-top: 2rem;
    margin-bottom: 2rem;


    .paymentStepsBlock {
        padding: 1rem;

        h1 {
            text-align: center;
        }
    }

    .paymentSteps {
        padding: 1rem;
        position: relative;
        height: 900px;
        font-size: 2rem;

        p {
            position: absolute;
            max-width: 40%;

            @media (max-width: 900px) {
                max-width: 80%;
            }
        }

        span {
            display: block;
            font-size: 2rem;
            font-weight: bold;
            margin-bottom: 0.5rem;
        }




     }


 .paymentStepsImg {
    position: absolute;
    top: 0%;
    left: 50%;
 }


   .step1 {
        top: -4%;
       left: 55%;
   } 

   .step2 {
       top: 17%;
       left: 10%;
   }

   .step3 {
       top: 40%;
       left: 55%;
   }

   .step4 {
       top: 70%;
       left: 10%;
   }

   .startListBlock {
    padding: 2rem;
}

   .startListImg {
       width: 640px;
   }

   .startListBlockItem {
       display: flex;
       flex-wrap: wrap;
       justify-content: space-around;
       align-items: center;
       align-content: center;
   }

   .startList {
     max-width: 50%;
       
   }







   @media (max-width: 900px) {

    .paymentSteps {
        height: 772px;
        font-size: 1rem;
    }

    .paymentStepsImg {
        left: 2%;
     }
    
    .step1, .step2, .step3, .step4 {
        left: 17%;
    } 
    .step1 {
        top: -4%;
   } 

    .step2 {
        top: 25%;
    }

    .step3 {
        top: 50%;
    }

    .step4 {
        top: 81%;
    }

    .startListImg {
        width: 310px;
    }

    .startList {
        max-width: 100%;
          
      }
    
  
   }

    
}