.SideMenu {
    height: 100%;
    width: 360px;
    position: fixed;
    z-index: 999;
    top: 0;
    right: 0;
    background-color: white;
    overflow-x: hidden;
    overflow-y: auto;
    transition: all 200ms ease-in-out;
    padding: 2rem 1rem;
    transform: translateX(120%);
    box-shadow: -10px 0px 10px 1px #aaaaaa;

    
}

.link {
    display: block;
    transition: all 200ms ease-in-out;
    font-weight: bold;
    font-size: 26px;
    line-height: 21px;
    color: black;
    cursor: pointer;
}

.link:hover {
  color: #A31FED;
}

.dropdown {
    margin-top: 3rem;
    padding-left: 40px;
}

.open {
    transform: translateX(0%)
}

.accButton {
    margin-left: 2rem;
    width: 85%;
}