.TopBlockWithImageNEW {

  position: relative;
  width: 100%;


  .topBlockImg {
    width: 100%;
  }

  .topBlockItem {
    position: absolute;
    top: 50%;
    width: 100%;
    transform: translateY(-50%);
  }

  .topBlockCentered {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin: 0 auto;
    height: 1000px;
    width: 90%;
    max-width: 1400px;
    padding-left: 2rem;
  }

  .topButton {
  }

  .topBlockItemText1 {
    font-size: 36px;
    font-weight: bold;
    color: white;
}

  .topBlockItemText2 {
    font-size: 16px;
    color: white;
    line-height: 20px;
  }

  .textBlock {
    width: 100%;
    max-width: 400px;
  }

  .buttonsBlock {
    position: absolute;
    display: flex;
    right: 0;
    bottom: 3px;
    width: 100%;
    max-width: 900px;
    color: white;
    font-weight: 900;
    font-size: 18px;
    height: 200px;
    cursor: pointer;
  }

  .selfEmployed {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    background-color: black;

    &:hover {
      opacity: 0.8;
    }

  }

  .legalEntity {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    background-color: #807CFA;
    // padding: auto;
    &:hover {
      opacity: 0.8;
    }
  }

  @media (max-width: 500px) {
    .topBlockItemText2 {
      display: none;
    }
  }



  @media (max-width: 900px) {
    .topBlockItemText1 {
      font-size: 16px;
    }

    .topBlockItemText2 {
      font-size: 12px;
      line-height: 16px;
    }

    .topBlockItem {
      bottom: 0;
    }
  }

  @media (max-width: 1400px) {
    .buttonsBlock {
      position: static;
      margin: -3px auto;
      height: 100px;
    }

    .topBlockCentered {
      height: auto;
    }
  }









  
}