.Socials {

    padding-left: 2rem;
    // margin-bottom: 2rem;

    img {
        margin-left: 0.5rem;
        width: 24px;
        transition: all 0.2s ease-in-out;

        

        &:hover {
            // background-color: black;
            transform: scale(1.2);
        }
    }

    .text {
        padding-left: 0.5rem;
        color: #74698C;
    }

    .imagesBlock {
        position: relative;
        top: 10px;
    }

    
}

.isFooter {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 300px;
    padding-bottom: 0;
    // padding-top: 2rem;
    padding-left: 1rem;

    p {
        padding-bottom: 0;
        margin-bottom: 0;
        font-size: 16px;
    }

    @media (max-width: 900px) {
        .text {
            padding-left: 0rem;
        }

        
    }

}