.InfoBlock {

    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: center;

    .infoBlockText {

        background-color: #B097BF;
        padding: 3rem;
        margin: 2rem;
        border-radius: 50px;
        max-width: 40%;

        p {
            color: white;
            font-size: 18px;
        }

        h1 {
            font-size: 24px;
        }
    }

    .image {
        width: 450px;
    }

    .btn {
        color: white;
    }

   


    @media (max-width: 900px) {

        .infoBlockText {
            max-width: 100%;
            padding: 1rem;
            margin: 1rem;

            p {
                font-size: 1rem;
            }

        }

        .image {
            width: 270px;
        }

        .btn {
            padding: 1rem 1rem;
        }
        .btnBlock {
            text-align: center;
        }

    }



}