.LegalEntity {



    .topBlock {
        display: flex;
        flex-direction: column;
        align-content: space-between;
        justify-content: space-between;

        position: relative;
        padding: 2rem;
        margin-left: 9rem;
        max-width: 40%;
        height: 500px;

    }

    .topBlockItem {
        margin-bottom: 1rem;
        text-align: justify;

    }

    .grayFontText {
        color: #808080;
        font-size: 1.5rem;
    }


    .supportInfo {
        display: flex;
    }

    .supportInfoItem {
        margin-right: 2rem;

        &:last-of-type {
            margin-right: 0;
        }
    }

    .vepayMobileApp {
        position: absolute;
        top: 20%;
        right: 20%;
        z-index: 99;
        width: 340px;
    }

    .aboutUs {
        position: relative;
        padding: 1rem;
        margin-left: 9rem;
        padding-top: 5%;
        padding-bottom: 5%;
        max-width: 40%;
        text-align: justify;
        // height: 500px;

    }

    .infoNavBlock {
        padding: 1rem;
    }

    .infoNav {
        display: flex;
        justify-content: center;
        align-items: center;

    }

    .infoNavItem {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0.5rem;

        margin-right: 0.5rem;
        height: 200px;
        width: 200px;
        text-align: center;
        font-weight: bold;
        font-size: 24px;
        cursor: pointer;
        border-radius: 50%;
        

        &:hover {
            background-color: #3ADF54;
        }

        &:last-of-type {
            margin-right: 0;
        }
    }

    .active {
        background-color: #3ADF54;
    }

    .productsBlock {

        .header {
            text-align: center;
        }

        .products {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            transition: all 0.2s ease-in-out;
        }
    }

    .partnerProgramme {
        display: flex;
        position: relative;
        


    }

    .partnerProgrammeBlock {
        display: flex;
        flex-wrap: wrap;
    }

    .partnerProgrammeHalfCircle {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 50em;
        height: 15em; 
        border-radius: 50% 50% 0 0 / 100% 100% 0 0;
        background: #B097BF;
        padding-left: 1rem;


        p {
            color: white;
            font-size: 1.2rem;
            font-weight: bold;
        }
    }

    .HalfCircleContent {
        padding-top: 5em;
        width: 80%;
    }

    .partnerProgrammeBlockBtn {
        position: relative;
        top: 140px;
        left: 50px;

        @media (max-width: 1100px) {
            top: 0;
            left: 0;
            margin: auto;
       
        }
    }

    

// =====================================================================================================




    @media (max-width: 900px) {
        .vepayMobileApp {
            display: none;
        }

        .supportInfoItem {
            margin-right: 1rem;
        }

        .topBlock, .aboutUs {
            max-width: 100%;
            margin-left: 0;
            height: auto;
            text-align: left;
        }

        .topBlockItem {
            text-align: left;
        }

        .infoNavItem {
            font-size: 1rem;
            height: 100px;
            width: 100px;
        }

        .partnerProgrammeHalfCircle {
            width: 100%;
        }

        .HalfCircleContent {

            h1 {
                font-size: 1.3rem;
            }

            p {
                font-size: 1rem;
            }

           
        }
    }

    @media (max-width: 1350px) {
        .vepayMobileApp {
            display: none;
        }
        .topBlock, .aboutUs {
            max-width: 100%;
            margin-left: 0;
            height: auto;
            text-align: left;
        }
    }

    
}