.Tariffs {

  .topBlock {
    align-items: center;
    width: 80%;
    margin: auto;
  }


  .topText {
    color: gray;
    font-size: 1.5rem;
  }

  .topImg {
    width: 437px;
  }

  .tariffsBlock {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .bottomTextBlock {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .bottomText {
    width: 60%;
    text-align: center;
    font-size: 1.5rem;
    color: gray;
  }
  
  .cardText1 {
    color: gray;
    font-weight: bold;
    font-size: 1.5rem;

  }

  select {
    width: 200px;
    height: 2rem;
    background-color: #F2F7FC;
    color: gray;
    font-weight: bold;
    font-size: 1.2rem;
    
  }




  @media (max-width: 900px) {
    .topImg {
      width: 320px;
    }

    .bottomText  {
      width: 90%;
    }
  }


















    
}