.AboutUs {


  .ourTeamBlock {
   
    position: relative;
    text-align: center;
    height: 1200px;
    padding: 2rem;
  }

  .imgBlock1, .imgBlock2, .imgBlock3 {
    position: absolute;
    width: 500px;
    text-align: center;
    margin-bottom: 2rem;

    img {
      width: 500px;
    }

    span {
      color: gray;
      font-size: 1.5rem;
      font-weight: bold;
    }
  }

  .imgBlock1 {


  }

  .imgBlock2 {
    right: 100px;
 
  }

  .imgBlock3 {
    bottom: 0;
    left: 200px;
   
  }

  @media (max-width: 1300px) {
    .ourTeamBlock {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      height: 1500px;
      padding-left: 1rem;
      padding-right: 1rem;
    }
    .imgBlock1, .imgBlock2, .imgBlock3 {
      position: relative;
      width: 330px;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      

      img {
        width: 320px;
      }
    }
  }









    
}