.ImageWithShadow {

    $palePurple: #807CFA;

   
        box-shadow:64px 69px 0px 0px $palePurple;
        -webkit-box-shadow:64px 69px 0px 0px $palePurple;
        -moz-box-shadow:64px 69px 0px 0px $palePurple;
        margin-bottom: 5rem;
        margin-right: 5rem;
        border-radius: 14%;
        width: 550px;

        @media (max-width: 900px) {
            width: 350px;
        }

 


}


.block {
    display: flex;
    justify-content: center;
    align-items: center;
}