.CardWithButton {

  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  width: 60%;
  padding: 1rem;
  margin: 1rem;
  border: 7px solid gray;
  border-radius: 28px;


  


  .cardColumn {
    display: flex;
    flex-direction: column;
   
    width: 40%;
    margin-bottom: 1rem;
  }

  .image {
    width: 289px;
  }

  


  @media (max-width: 900px) {

    width: 90%;

    .cardColumn {
      width: 90%;
      justify-content: center;
      align-items: center;

    }
    .image {
      width: 260px;
    }
    .header {
      text-align: center;
    }
  }




}