.SolutionsCard {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    background-color: white;

    border: 2px gray solid;
    margin-bottom: 1rem;
    margin-right: 1rem;
    padding: 1rem;
    border-radius: 28px;
    width: 40%;
    height: 260px;
    transition: all 0.2s ease-in-out;


    .SolutionsCardText {
        width: 60%;
        text-align: justify;

        h1 {
            text-align: left;
        }
    }

    .image {
        // width: 100px;
    }


    &:hover {
        border-color: #A31FED;
        // transform: scale(1.2);
    }


    @media (max-width: 900px) {

        justify-content: center;
        height: 322px;

        &:hover {
            // transform: scale(1);
        }
        .SolutionsCardText {
            width: 100%;
        }
    }

    @media (max-width: 1500px) {
            width: 90%;
            margin-right: 0rem;
    }








}