.DevelopersMain {

  flex-grow: 1;
  min-height: 100%;
  background-color: hsl(0, 0%, 90%);
  padding: 1rem;
  overflow-x: hidden;

  
/* Нечетные строки */
tbody tr:nth-child(odd){
  background: #fff;
  border-bottom: 1px solid #322C40;
}
 
/* Четные строки */
tbody tr:nth-child(even){
  background: #F7F7F7;
  border-bottom: 1px solid #322C40;
}

table td {
  font-size: 16px;
  line-height: 21px;
  padding: 1rem 0.5rem;
}

table {
  width: 100%;
  margin-bottom: 1rem;
  overflow-x: auto;
  display: block;
}
code {
  background-color: #14121A;
  padding: 1rem;
  display: block;
  margin-bottom: 1rem;
  color: #FFFFFF;
  overflow-x: auto;
}

  








}