.Button {
    padding: 1rem 4rem;
    font-size: large;
    font-weight: bold;
    border-radius: 28px;
    cursor: pointer;
    transition: all 0.2s ease-out;
    outline: none;

    @media (max-width: 900px) {
        padding: 0.5rem 2rem;
        font-size: 1rem;
        font-weight: bold;
    }


}

.purple {
    color: #A31FED;
    border-color: #5C0BFF;
    background-color: transparent;

    &:hover {
        background-color: #A31FED;
        color: white;
    }

}

.green {
    background-color: #3ADF54;
    border: none;
    color: white;

    &:hover {
        background-color: #A31FED;
    }
}

.purpleBG {
    color: white;
    border-color:#A31FED;
    background-color: #A31FED;

    &:hover {
        background-color: transparent;
        color: #A31FED;
    }
}

.violetBG {
    color: white;
    border-color: #5C0BFF;
    background-color: #5C0BFF;

    &:hover {
        background-color: transparent;
        color: #5C0BFF;
    }


   

}