.Partners {


  .talkingPeopleBlock {
    justify-content: center;
    align-items: center;
  }

  .inputRangeBlock {
    padding: 2rem;
    border: 7px solid #D3D2D4;
    border-radius: 28px;
    text-align: center;
  }

  .resultBlock {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }

  .btnWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .result {
    display: inline-block;
    font-size: 2rem;
    font-weight: bold;
    width: 142px;
  }

  .talkingPeopleImg {
    text-align: center;
  }
  .advantagesList {
    padding-left: 5rem;
    padding-right: 5rem;
    @media (max-width: 1000px) {
      padding-left: 0;
    padding-right: 0;
    }
  }


  @media (max-width: 900px) {
    .talkText {
      font-size: 1.2rem;
    }
    .btnWrapper {
      width: 100%;
    }

    .inputRangeBlock {
      padding-left: 0;
      padding-right: 0;
    }
  }





























    
}