.TextWithTick {
    font-size: 1.8rem;
    font-weight: bold;

    &::before {
        content: '\2713';
        position: relative;
        left: -15px;
    }

    @media (max-width: 900px)  {
        font-size: 1.5rem;
    }

}

.violet {


    &::before {
        // content: '\2713';
        color: #A31FED;
    }

}

.green {


    &::before {
        // content: '\2713';
        color: #03EE65;
    }


}

.purple {
    

    &::before {
        // content: '\2713';
        color: #807CFA;
    }
}

