.ConnectModal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease-in-out;
  overflow: hidden;
  z-index: 99999;


  input:not([type='checkbox']), textarea {
    width: 300px;
    border: none;
    border-bottom: 2px solid gray;
    background: none;
    outline: none;
    font-weight: 500;
    font-size: 1.2rem;

    &:focus {
      background: transparent;
      border-color: blueviolet;
    }
  }


  input:-webkit-autofill {
    -webkit-box-shadow: inset 0 0 0 50px #fff !important; /* Цвет фона */
    box-shadow: inset 0 0 0 50px #fff !important; /* Цвет фона */
    -webkit-text-fill-color: black !important; /* цвет текста */
    color:black !important; /* цвет текста */
}

textarea {
  resize: vertical;
}

  .modalFormItem {
    width: 300px;
    font-weight: 500;
    margin-top: 2rem;
  }
  

  .ConnectModalBody {
    padding: 5rem;
    position: relative;
    background-color: white;
    border-radius: 20px;
    z-index: 99999;
    opacity: 1;
    width: 375px;
    overflow-y: auto;

    p {
      font-size: 1.2rem;
      font-weight: bold;
    }
 

  }

  .modalFormBlock {
    // overflow-y: auto;

  }

  .modalForm {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    // height: 800px;
  }

  .modalFormBtn {
    margin-top: 2rem;
  }

  .modalCloseButton {
    position: absolute;
    font-size: 2rem;
    right: 20px;
    top: 15px;
    cursor: pointer;
    transition: all 0.2s ease-in-out;

    &:hover {
        color: #A31FED;
    }
}

.isDisabled {
  cursor: not-allowed;
  background-color: gray;
  border-color: gray;

  &:hover {
    color: white;
  }
}

@media (max-width: 900px) {
  .ConnectModalBody {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
    width: 340px;
  }
}

}

.backDrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 9999;

}


