.HomePage {

   .grayBGIntro {
    background-color: #464646;
    min-height: 416px;
    border-radius: 27px;
    color: white;
    margin: 7rem 1rem;
    box-shadow: 0 0 30px rgba(0,0,0,0.5); /* Параметры тени */

   }
  .centeredBlock {
    display: flex;
    justify-content: space-evenly;
    // align-items: center;
    
   
    padding: 1rem;
     max-width: 1200px;
     margin: auto;
  }

  .introBlock {
    position: relative;
    width: 100%;
    max-width: 300px;
  }

  .creativeImg {
    width: 370px;
    position: absolute;
    top: -117px;
    left: -70px;
  }

  .advantages {
    display: flex;
    max-width: 1200px;
    margin: 7rem auto;

  }

  .advantagesBlock {
    position: relative;
    max-width: 600px;
    width: 90%;
    padding: 1rem;
  }

  .advantagesText {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    // align-items: center;
    max-width: 400px;
    margin: auto;
    height: 100%;
    p {
      color: #959595;
    }
  }

  .mobileAppImg {
    display: block;
    max-width: 309px;
    width: 100%;
    margin: auto;
    box-shadow: 0 0 30px rgba(0,0,0,0.5); /* Параметры тени */
    border-radius: 29px;
  }

  .decorations {
    position: absolute;
    top: -80px;
    left: 42px;
  }

  .payMethods {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;

    img {
      max-width: 45%;
    }

  }

  .solutions {
    display: flex;
    max-width: 1300px;
    margin: 7rem auto;

  }

  .solutionsBlock {
    position: relative;
    max-width: 500px;
    width: 90%;
    padding: 1rem;
    margin: 0 auto;
  }

  .solutionsText {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin: auto;
    height: 100%;
    p {
      color: #959595;
    }
  }

  .solutionsImg {
    position: relative;
    max-width: 497px;
    width: 100%;
    top: 45px;

  }

  .purpleBG {
    position: absolute;
    top: -80px;
    left: -42px;
    z-index: -1;
  }

  .banks {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;

    img {
      max-width: 30%;
    }
  }

  .prefooterImg {
    width: 100%;
  }

  

  @media (max-width: 1350px) {
    .decorations,
    .purpleBG {
      display: none;
    }
  }

  @media (max-width: 900px) {
    .hidePic {
      display: none;
    }
    .grayBGIntro,
    .advantages,
    .solutions {
      margin: 2rem 1rem;
    }
  }

  @media (max-width: 700px) {
    .centeredBlock,
    .advantages,
    .solutions  {
      flex-direction: column;
    }

    .introBlock {
      margin: auto;
    }
    .solutionsImg {
      position: static;
    }
  }


    
}