.Credits {



    .advantages, .loanCalculationBlock, .loanConditions {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }

   .loanConditions {
      justify-content: center;

   }

    .loanHeader {
      text-align: center;
    }

    .loanText {
      font-size: 1.2rem;
      font-weight: normal;
    }

    .loanCalculationBlock {
      padding: 2rem;
      color: white;
    }

    .loanCalculationCard {
      width: 80%;
      border-radius: 49px;
      margin: auto;
      padding: 1rem;
      background-color: #B097BF;
      
    }

    .rangeBlock {
      text-align: center;
      margin-bottom: 2rem;
    }

    .rangeWrapper {
      
    }

    .resultBlock {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      margin: 6rem;
    }

    .resultItem {
      width: 40%;
    }

    


    @media (max-width: 900px) {
      .loanCalculationBlock {
        padding: 0;
        padding-bottom: 1rem;
      }
      .resultBlock {
        margin: 1rem;
      }
      .resultItem {
        width: 100%;

      }
    }




}