
    .item {
        margin-right: 1rem;
        margin-bottom: 1rem;
    }

    .link {
        display: block;
        transition: all 200ms ease-in-out;
        font-weight: bold;
        // font-size: 26px;
        line-height: 21px;
        color: black;
        cursor: pointer;
    }

    .link:hover {
      color: #A31FED;
    }

    
    .header {
        display: flex;
        flex-wrap: wrap;
        list-style: none;
        margin: 0;
        padding-left: 0;
        align-items: center;

        .link {
            position: relative;
            font-size: 19px;
            color: white;
            // border-bottom: 3px pink transparent;

            // &:hover{
            //     border-bottom: 3px white solid;
            // }
        }

        .link:before {
            display: block;
            position: absolute;
            // left: 0; /*изменить на right:0;, чтобы изменить направление подчёркивания */
            bottom: -5px;
            width: 0;/*задаём длинну линии до наведения курсора*/
            height: 3px; /*задаём ширину линии*/
            background-color: white; /*задаём цвет линии*/
            content: "";
            transition: width 0.3s ease-out; /*задаём время анимации*/
        }
        
        .link:hover:before,
        .link:focus:before {
            width: 100%; /*устанавливаем значение 100% чтобы ссылка подчёркивалась полностью*/
        }

        @media (max-width: 1150px) {
            .item {
                display: none;
            }
           
        }

        
    }

    .sidebar {
        display: flex;
        flex-direction: column;
        list-style: none;
        .link {
            font-size: 24px;
        }

        .item {
            margin-bottom: 1rem;
        }
    }

    .button {
        display: block;
    }











