.ReceivingPayments {



    .tickedBlock1 {
        margin-right: 1rem;
        // margin-bottom: 1rem;
        max-width: 45%;
    
        @media (max-width: 1500px) {
            max-width: 100%;
        }

    }

    .tickedBlock2 {
        margin-right: 1rem;
        // margin-bottom: 1rem;
        max-width: 45%;
    
        @media (max-width: 1500px) {
            max-width: 100%;
        }
    }

    .tickedBlocks {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: 2rem;

