.InputRange {

  display: flex;
  justify-content: center;
  flex-wrap: wrap;


  .inputRangeBlock {
    display: flex;
    height: 80px;
    width: 85%;

    

  }

  .rangeTrack {
    height: 20px;
    width: 100%;
    border-radius: 15px;
    align-self: center;
  }

  .thumb {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 42px;
    width: 42px;
    border-radius: 50%;
    background-color: #FFF;
    box-shadow: 0px 2px 6px #AAA;
    outline: none;

  }

  .thumbTopText {
    position: absolute;
    top: -28px;
    color: #fff;
    font-weight: bold;
    font-size: 14px;
    font-family: Arial,Helvetica Neue,Helvetica,sans-serif;
    padding: 4px;
    border-radius: 4px;
    background-color: #807CFA;
    white-space: nowrap;

  }








}