

    $palePurple: #807CFA;

    .topBlock {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
        align-content: center;
        padding: 4rem;

        @media (max-width: 900px) {
            padding: 1rem;
         }
    }

    .topBlockItem {
        max-width: 40%;

        @media (max-width: 1200px) {
            max-width: 70%;
         }

        @media (max-width: 900px) {
           max-width: 100%;
        }
    }

    .topBlockImg {
        box-shadow:64px 69px 0px 0px $palePurple;
        -webkit-box-shadow:64px 69px 0px 0px $palePurple;
        -moz-box-shadow:64px 69px 0px 0px $palePurple;
        margin-bottom: 5rem;
        margin-right: 5rem;
        border-radius: 14%;
        width: 550px;

        @media (max-width: 900px) {
            width: 350px;
        }

    }

    .topBlockItemText1 {
        font-size: 2rem;
        font-weight: bold;
    }

    .topBlockItemText2 {
        font-size: 1.5rem;
        color: #959595;
    }

    

    .extraTextBlock {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

    }

    .extraTextItem {
        width: 45%;

        p {
            color: #959595;
        }
    }

    









