.Footer {

    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    // justify-content: space-around;
    // align-items: center;
    padding: 1rem;
    // background-color: #F2F7FC;
    margin: 1rem auto;
    width: 90%;
    max-width: 1300px;

    .footerNavbar {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-end;
        justify-content: space-between;
        width: 95%;
        max-width: 1100px;
        margin: 0 auto;
    }

    .footerNavbar2 {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        justify-content: space-between;
        width: 95%;
        max-width: 1100px;
        margin: 0 auto;


        .item {
            margin-top: 0rem;
            margin-bottom: 1rem;

            a {
                color: gray;
                &:hover {
                    color: #A31FED;
                  }
            }
        }
    }

    .visaMasterBlock {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        max-width: 300px;
        padding-top: 0rem;
        padding-left: 1rem;

        span {
            padding-left: 10px;
        }

        img {
            width: 183px;
        }

    }

    hr {
        width: 100%;
    }

    .list {
        display: flex;
        flex-wrap: wrap;
        list-style: none;
        // flex-grow: 1;
        margin: 0;
        padding-left: 0;
        align-items: center;
    }

    .item {
        margin-right: 1rem;
        margin-top: 1rem;
    }

    .link {
        display: block;
        transition: all 200ms ease-in-out;
        font-weight: bold;
        font-size: 16px;
        line-height: 17.49px;
        color: black;
        cursor: pointer;
    }

    .link:hover {
      color: #A31FED;
    }


    @media (max-width: 900px) {
        .footerNavbar,
        .footerNavbar2 {
            justify-content: center;
        }

        .footerNavbar {
            .list {
                padding-left: 1rem;
            }
        }

        .visaMasterBlock {
            span {
                padding-left: 0px;
            }
        }
    }


    // .mediaInfo {
    //     max-width: 40%;
    //     text-align: justify;

    //     @media (max-width: 900px) {
    //         max-width: unset;
    //     }
    // }















    
}