.Header {
    display: flex;
    position: absolute;
    z-index: 999;
    left: 0;
    right: 0;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    max-width: 1400px;
    margin: 0 auto;

    .accButton {
        color: white;
    }

    @media (max-width: 700px) {
        .accButton {
            display: none;
        }
      
    }

    

}

.logo {
    margin-left: 0px !important;
}