.DevelopersSideMenu {

  display: flex;
  flex-direction: column;
  min-height: 100%;
  min-width: 300px;
  width: 300px;
  padding: 1rem;

  .link {
    display: block;
    transition: all 200ms ease-in-out;
    font-weight: bold;
    font-size: 26px;
    line-height: 21px;
    color: black;
    cursor: pointer;

    &:hover {
      color: #A31FED;
    }
  }

  .mb1rem {
    margin-bottom: 1rem;
    margin-top: 0.5rem;

  }






  @media (max-width: 900px) {
    display: none;
  }





}