.CardPurpleBG {
    display: flex;
    flex-direction: column;
    // justify-content: space-between;
    height: 466px;

    background-color:  #B097BF;
    margin-right: 2rem;
    border-radius: 20px;
    width: 15%;
    padding: 1rem;
    margin: 1rem;
    transition: all 0.2s ease-in-out;



    .btn {
        color: white;
        align-self: center;
        width: 90%;
    }

    .img {
        align-self: center;
    }

    .text {
        color: white;
        flex-grow: 1;
    }


    &:hover {
        transform: scale(1.1);
    }


    @media (max-width: 1400px) {
        width: 35%;

    }

    @media (max-width: 900px) {
        width: 50%;
    }

    @media (max-width: 500px) {
        width: 80%;
    }




}