.DropDown {
  position: relative;
  transition: all 200ms ease-in-out;
  margin-right: 1rem;

  .dropDownBody {
    display: flex;
    flex-direction: column;
    background-color: white;
    width: 285px;
    z-index: 9999;
    transition: all 0.2s ease-in-out;
    padding-bottom: 1rem;
    padding-left: 1rem;
  }

  .link {
    margin-top: 1rem;
    font-size: 19px;
    font-weight: bold;
    // color: black;
    transition: all 200ms ease-in-out;
    
    &:hover {
      color: #A31FED;
    }
  }

  .headerDropDown {
    position: absolute;
    top: 30px;
    background-color: hsl(0, 0%, 90%);
  }

  .sideMenuDropDown {
    position: relative;
    top: 5px;
  }

  .show {
    display: flex;
  }

  .hide {
    display: none;
  }

  .arrow {
    width: 14px;
    height: 14px;
    margin-left: 5px;
  }

  .title {
    position: relative;

    &::after {
      display: inline-block;
      position: absolute;
      content: '›';
      font-size: 29px;
      transform: rotate(90deg);
      margin-left: 9px;
    }
  }

  .arrowUp {

    &::after {
      content: '›';
      transform: rotate(-90deg);
      margin-left: 3px;
    }
  }







}