.SkewedBlock {

    position: relative;
    background: url('../../img/shield-img.png') no-repeat 90% top;
    background-size: 68px;
    background-color: #F2F7FC;
    margin-bottom: 3rem;
    padding: 1rem;

    &:after {
      background-color: #F2F7FC;
        bottom: 0;
        content: '';
        display: block;
        height: 50%;
        left: 0;
        position: absolute;
        right: 0;
        transform: skewY(-5deg);
        transform-origin: 100%;
        z-index: -1;
      }

     &:before {
      background-color: #F2F7FC;
        bottom: 0;
        content: '';
        display: block;
        height: 50%;
        top: 0;
        left: 0;
        position: absolute;
        right: 0;
        transform: skewY(-5deg);
        transform-origin: 0%;
        z-index: -1;
      }









}