.Blog {

  .topBlock {
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  .topImg {
    width: 437px;
  }

  .newsBlock {
    justify-content: center;
    text-align: center;

  }

  .newsColumn {

    border-radius: 10%;
    transition: all 0.2s ease-in-out;
    margin-bottom: 1rem;


    &:hover {
      background-color: hsl(0, 0%, 80%);
    }
  }





  @media (max-width: 900px) {
    .topImg {
      width: 320px;
    }
   
  }




}