.BlockWithBorderAndButton {
  display: flex;
  flex-direction: column;


  width: 40%;
  padding: 1rem;
  margin: 1rem;
  border: 5px solid #807CFA;
  border-radius: 49px;


  .btn {
    align-self: center;
  }

  .content {
    flex-grow: 1;
    padding: 1rem;
  }

  @media (max-width: 900px) {
    width: 100%;
 }





}